import React, { Component } from 'react';
import { browserHistory } from 'react-router';
import AppBar from 'material-ui/AppBar';
import Checkbox from 'material-ui/Checkbox';
import { orange500 } from 'material-ui/styles/colors';
import EmailInput from './EmailInput';
import SaveButton from './SaveButton';
import { getUser } from '../utils/utils';

const styles = {
  appbar: {
    backgroundColor: '#777'
  },
  input: {
    width: '70%',
    marginTop: '30px'
  },
  button: {
    width: '40%',
    marginTop: '20px'
  },
  left: {
    width: '48%',
    float: 'left',
    paddingLeft: '10px'
  },
  right: {
    textAlign: 'left',
    width: '48%',
    float: 'left'
  },
  box: {
    fill: orange500
  }
};

class Dashboard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user: {},
      token: {},
      newEmail: '',
      newEmailConfirm: '',
      marketing: false,
      articles: false,
      digest: false
    }
  }
  componentWillMount() {
    const userId = this.props.params.id;
    getUser(userId, (data) => {
      this.setState({ user: data.user, token: data.token })
      browserHistory.push('/users/' + userId + '?email=' + data.user.email + '&token=' + data.token.nonce)
    });
  }
  handleEnter(e) {
    // If enter key pressed
    if (e.charCode === 13) {
      this.handleSubmit();
    }
  }
  handleNewEmailChange(e, newString) {
    this.setState({ newEmail: newString });
  }
  handleNewEmailConfirmChange(e, newString) {
    this.setState({ newEmailConfirm: newString });
  }
  handleMarketingCheck() {
    console.log('marketing check');
  }
  handleArticlesCheck() {
    console.log('articles check');
  }
  handleDigestCheck() {
    console.log('digest check');
  }
  handleNoEmailsCheck() {
    console.log('no emails check');
  }
  handleSubmit() {
    console.log('submit');
  }
  render() {
    let greeting = 'Welcome, ' + this.state.user.name + '!';
    return (
      <div>
        <AppBar
          style={styles.appbar}
          title={greeting}
          showMenuIconButton={false} />
        <h3>Email Preferences</h3>
        <div
          className="dashboard-left"
          style={styles.left}>
          <EmailInput
            styles={styles.input}
            current={this.state.newEmail}
            text="Update Email Address"
            change={(event, newString) => this.handleNewEmailChange(event, newString)}
            keyPress={(event) => this.handleEnter(event)} />
          <EmailInput
            styles={styles.input}
            current={this.state.newEmailConfirm}
            text="Confirm New Email Address"
            change={(event, newString) => this.handleNewEmailConfirmChange(event, newString)}
            keyPress={(event) => this.handleEnter(event)} />
        </div>
        <div
          className="dashboard-right"
          style={styles.right}>
          <h4>I want emails about:</h4>
          <Checkbox
            iconStyle={styles.box}
            label="Marketing"
            onCheck={this.handleMarketingCheck} />
          <Checkbox
            iconStyle={styles.box}
            label="Articles"
            onCheck={this.handleArticlesCheck} />
          <Checkbox
            iconStyle={styles.box}
            label="Digest"
            onCheck={this.handleDigestCheck} />
          <Checkbox
            iconStyle={styles.box}
            label="No Emails"
            onCheck={this.handleNoEmailsCheck} />
        </div>
        <SaveButton
          styles={styles.button}
          click={(event) => this.handleSubmit(event)}/>
      </div>
    )
  }
}

export default Dashboard;



